.card-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
  transition: transform 0.1s ease-in-out;
  overflow: hidden;
  border: 1px solid #332a6e;
  background: #332a6e; 
  margin: 2rem 0rem;
  width: 93%;
}

.card-container:hover {
  transform: scale(1.02);
}

.card-image-container {
  display: flex;
  align-items: center;
  overflow: hidden;
  height: 28vh;
}

.card-image-container.pointer-cursor {
  cursor: pointer;
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-content {
  padding: 1.5em;
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* changed from center to space-between */
}

.card-title {
  color: #F5F7FA;
  font-size: 1.5rem;
  margin-bottom: 0.3em; /* reduced margin */
}

.card-description {
  color: #DDE2E9;
  line-height: 1.5;
  margin-bottom: 0.8em; /* reduced margin */
}

.card-tags {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0.8em; /* reduced margin */
}

.tag {
  background-color: rgba(47, 72, 88, 0.7);
  color: #F5F7FA;
  border-radius: 50px;
  padding: 0.5em 1em;
  font-size: 0.8rem;
  margin-right: 0.5em;
  margin-bottom: 0.5em;
}

.card-buttons {
  display: flex;
  gap: 1rem;
  justify-content: flex-start; /* buttons aligned to start */
}

.card-button {
  background-color: transparent; 
  color: #F5F7FA;
  border: 2px solid #F5F7FA; 
  border-radius: 50px;
  padding: 0.75em 1.5em;
  text-transform: uppercase;
  font-weight: bold;
  transition: all 0.3s ease;
  cursor: pointer;
}

.card-button:hover {
  color: #466962; /* change the text color to match the background */
  background-color: #F5F7FA; /* change the background color to match the previous text color */
}

/* ----------------------------------------------------------------------------------------------- */
.ReactModal__Overlay {
  background-color: rgba(0, 0, 0, 0.75) !important; /* Dark overlay */
}
.modal-style {
  background: black;
  padding: 20px;
  padding-bottom: 0px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 80%;
  max-width: 600px; /* Adjust based on your preference */
  margin: auto;
  position: fixed; /* Use fixed positioning */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /* Center the modal */
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* This will space out your header, content, and footer */
  overflow: hidden; /* Prevents scrollbars if the content fits */
  margin: auto;
}


.close-modal-button {
  position: absolute;
  z-index: 1000; 
  top: 10px;
  right: 15px;
  border: none;
  background: none;
  font-size: 20px;
  cursor: pointer;
  color: white; /* Ensure 'X' is visible */
}

.carousel .slide img {

  margin: auto;
}

.carousel .legend {
  display: none; /* Hide default legend */
}
.custom-legend {
  text-align: center;
  padding-top: 10px; /* Spacing between the image and the text */
  color: white; /* Text color */
}
.carousel .thumbs {
  display: none; /* Hide thumbnails */
}

.carousel .control-arrow, .carousel.carousel-slider .control-arrow {
  /* You can customize the arrows here */
}

.carousel .control-dots {
  display: none; /* Hide control dots */
}

/* Responsive styling */
@media (max-width: 768px) {
  .modal-style {
    width: 90%;
    padding: 15px;
  }

  .carousel .slide img {
    max-height: 300px;
  }
}

/* ----------------------------------------------------------------------------------------------- */

/* Responsive design for screens smaller than 600px */
@media (max-width: 5000px) {
  .card-container {
    grid-template-columns: 1fr;
  }

}
