/* Target the root element of the ASCI112 component and its children only */
.ASCI112-page {
    background: grey; /* Sets the background color of the ASCI112 page to light green */

    display: flex;
    flex-wrap: wrap;

  }

  .ASCI112-page h1,
  .ASCI112-page h2,
  .ASCI112-page h3,
  .ASCI112-page h4,
  .ASCI112-page h5,
  .ASCI112-page h6,
  .ASCI112-page p,
  .ASCI112-page ul,
  .ASCI112-page ol,
  .ASCI112-page li {
    margin: 0;
    color: white;
  }
  
  .ASCI112-side-left {
    padding-top: 4%;
    position: fixed;
    flex: 1;
    padding-left: 6%;
    max-width: 35%;
    height: 100%;
    overflow: auto;
  
  }
  
  .ASCI112-side-right {
    padding-top: 4%;
    flex: 1;
    max-width: 50%;
    margin-left: 50%;  /* Adjust according to the width of the left side */
  }
  

  .image-container img {
    width: 60%; /* Make images responsive, adjust as needed */
    padding: 5px; /* Add some space around images */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: adds a subtle shadow to images */
    margin-bottom: 10px; /* Space between images */
  }

  /* If screen size is 800px or less, switch to column layout */
  @media (max-width: 800px) {
    .ASCI112-page {
      flex-direction: column;
      height: auto;
      overflow-y: auto;  /* Enable scrolling on the entire app */
    }
  
    .ASCI112-side-left, .ASCI112-side-right {
      position: static;
      min-width: 0;
      max-width: 100%;
      margin-left: 0;
      height: auto;  /* Allow the height to grow */
      overflow: visible;  /* Default overflow behavior */
    }

  }
  